/* tslint:disable:max-line-length */

import { NestedOption } from '../../../core/nested-option';

import DevExpress from 'devextreme/bundles/dx.all';

export abstract class DxoHtmlEditorToolbar extends NestedOption {
    get commands(): Array<string> {
        return this._getOption('commands');
    }
    set commands(value: Array<string>) {
        this._setOption('commands', value);
    }

    get visible(): boolean {
        return this._getOption('visible');
    }
    set visible(value: boolean) {
        this._setOption('visible', value);
    }

    get fileSelectionItems(): Array<any | string> {
        return this._getOption('fileSelectionItems');
    }
    set fileSelectionItems(value: Array<any | string>) {
        this._setOption('fileSelectionItems', value);
    }

    get items(): Array<any | string | DevExpress.ui.dxHtmlEditorToolbarItem> {
        return this._getOption('items');
    }
    set items(value: Array<any | string | DevExpress.ui.dxHtmlEditorToolbarItem>) {
        this._setOption('items', value);
    }

    get container(): Element | JQuery {
        return this._getOption('container');
    }
    set container(value: Element | JQuery) {
        this._setOption('container', value);
    }
}
